@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {

	&.margin-based-transition {
		margin-right: 0;
	}

	opacity: 1;
	pointer-events: all;

	&.animate {
		animation: pulse 1s;
	}

	&:hover {
		opacity: 0.9;
		@include dpitBoxShadowAlfa(0.15);
	}

}

@mixin inactive {

	transform: translateX(100%);

	&.margin-based-transition {
		transform: none;

		&.margin-based-transition-58 {
			margin-right: -58px;
		}

		&.margin-based-transition-80 {
			margin-right: -80px;
		}

		&.margin-based-transition-120 {
			margin-right: -120px;
		}

		&.margin-based-transition-160 {
			margin-right: -170px;
		}

		&.margin-based-transition-250 {
			margin-right: -250px;
		}

		&.margin-based-transition-350 {
			margin-right: -350px;
		}

		@include atXS {
			margin-right: -167px;

			&.margin-based-transition-58 {
				margin-right: -58px;
			}

			&.margin-based-transition-80 {
				margin-right: -60px;
			}

			&.margin-based-transition-120 {
				margin-right: -84px;
			}

			&.margin-based-transition-160 {
				margin-right: -119px;
			}

			&.margin-based-transition-250 {
				margin-right: -175px;
			}

			&.margin-based-transition-350 {
				margin-right: -300px;
			}
		}
	}

	opacity: 0;
}

.dpit-controls {

	pointer-events: none;
	height: 100%;
	width: 100%;

	&__wrap {
		@include z-index(13);
		position: fixed;
		bottom: 0;
		right: 0;
		display: flex;
		align-items: flex-end;
		@include transitionMultiFast(padding, flex-wrap, align-items, justify-content, margin-left);
		padding: 0 30px 30px;

		@include atM {
			padding: 0 20px 20px;
			flex-wrap: wrap-reverse;
			align-items: flex-start;
			justify-content: flex-end;
			margin-left: auto;
		}
	}

	&__multi-close {
		position: fixed;
		top: 0px;
		right: 0px;
		margin: 15px 20px 0;
		//margin: 0 !important;
		@include z-index(13);

		&--bottom-left {
			top: unset;
			right: unset;

			left: 0px;
			bottom: 40px;


			.dpit-multi-item-box__menu {
				transform-origin: 35px 35px;
				right: unset;
				left: 0;
				align-items: center;
				flex-direction: row;



				@include atXS {
					width: 100vw;
					flex-wrap: wrap;

					&--opened {
						transform: translateY(-50%);
					}

					//transform-origin: calc(100% - 34px) 34px;
				}

			}

			.dpit-multi-item-box__item:not(:first-child) {
				margin-left: 10px;
			}

			.dpit-controls__button--cancel-sub {
				text-align: left;
				margin-top: 5px;
				margin-bottom: 5px;

				@include atXS {
					min-width: 100px;
				}
			}

		}


		@include atM {
			margin: 15px 10px 0;
		}

		@include atXS {
			margin: 10px 10px 0px;
		}
	}

	&__button {
		@include primaryFont(500, $text-color-light, 18px, 1.5);
		text-transform: uppercase;
		pointer-events: none;
		cursor: pointer;
		justify-content: center;
		align-items: center;
		padding: 0px 20px;
		border-radius: 2px;
		opacity: 0;
		@include transitionMulti($transition-time-fast, ease-in, color, background-color, transform, opacity, margin-right);
		@include dpitBoxShadow;
		height: 48px;
		outline: none;
		border: none;
		@include z-index(13);
		color: rgba(var(--dpu-color-2-text), 1);

		background-color: rgba(var(--dpu-color-2), 1);
		display: none;
		margin-left: 10px;
		margin-bottom: 10px;
		text-align: center;

		&:first-child {
			margin-left: 0px;
		}

		span {
			opacity: 0;
			@include transitionFast(opacity);
		}

		&--edit-step {
			border-radius: 2px;
			width: 48px;
			height: 48px;
			color: rgb(var(--dpu-color-b-text));
			background-color: rgb(var(--dpu-color-b));
			padding: 0;
			cursor: pointer;
		}

		&--change-selection {
			color: rgba(var(--dpu-color-b-text), 1);
			background-color: rgba(var(--dpu-color-b), 1);
		}

		&--pause-builder {
			position: fixed;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			border-radius: 50%;
			color: rgb(var(--dpit-uac-prim));
			background-color: rgba(var(--dpit-uac-prim-bg), 0.97);

		}

		&--save {
			color: rgb(var(--dpu-color-g-text));
			background-color: rgb(var(--dpu-color-g));
			position: relative;
		}

		&--alt {
			border-radius: 2px;
		}

		&--active:not(.dpit-controls__button--cancel-sub) {
			display: flex;

			@include active;

			&.dpit-controls__button--in-transition {
				@include inactive;
			}
		}

		&--add-first-step {
			position: fixed;
			align-items: center;
			justify-content: center;
			font-size: 25px;
			min-height: 100px;
			left: calc(50% - 175px);
			top: calc(50% - 50px);
			background-color: rgb(var(--dpu-color-g));
			color: rgba(var(--dpu-color-g-text), 1);
			border-radius: 2px;
			text-transform: uppercase;

			&:hover {
				opacity: 0.97 !important;

			}

			@include atXS {
				left: calc(50% - 150px);
				min-height: 70px;
				top: calc(50% - 35px);
			}

		}



		&.margin-based-transition-120 {
			min-width: 120px;
		}

		&.margin-based-transition-170 {
			min-width: 170px;
		}

		&.margin-based-transition-250 {
			min-width: 250px;
		}

		&.margin-based-transition-350 {
			min-width: 350px;
		}

		@include atXS {
			font-size: 16px;
			font-weight: 700;

			&.margin-based-transition-120 {
				min-width: 84px;
			}

			&.margin-based-transition-170 {
				min-width: 119px;
			}

			&.margin-based-transition-250 {
				min-width: 175px;
			}

			&.margin-based-transition-350 {
				min-width: 300px;
			}
		}


		&--processing {
			cursor: default;
			pointer-events: none;
			color: transparent;
			-webkit-user-select: none;
			user-select: none;


			&.dpit-controls__button--save {
				background-color: rgba(var(--dpu-color-b), 1);
			}

			.dpit-anim-rb-builder.dpit-controls__running-bullets-anim {
				top: 50%;
				transform: translate(-50%, -50%);
				//top: calc(50% + 2px);

				ul {
					flex-wrap: nowrap;
					flex-shrink: 0;

					li {
						background-color: rgba(var(--dpu-color-b-text), 0.25) !important;
						width: 6px;
						height: 6px;


						&.dpit-bullet-on {
							background-color: rgba(var(--dpu-color-b-text), 1) !important;
							transform: translateY(-10%);

							&--1 {
								transform: translateY(-5%);
								background-color: rgba(var(--dpu-color-b-text), 0.918) !important;
							}

							&--2 {
								transform: translateY(-2%);
								background-color: rgba(var(--dpu-color-b-text), 0.718) !important;
							}
						}
					}
				}
			}

		}

		&--processed {
			cursor: default;
			color: rgba(var(--dpu-color-g-text), 1);
			background-color: rgba(var(--dpu-color-g), 1);
			pointer-events: none;
			//animation: pulse 1s; 

		}

		&--error {
			cursor: default;
			pointer-events: none;
			//animation: pulse 1s; 
			background-color: rgba(var(--dpu-color-r), 1);
		}

		&__icon {
			width: 55%;
		}
	}

	&__hint {
		opacity: 0.95;
		font-size: 18px;
		pointer-events: none;
		text-transform: uppercase;
		background-color: rgba(255, 255, 255, 0.733);
		font-weight: bold;
		text-align: center;
		padding: 10px;
		display: none;
		max-width: 300px;

		&--active {
			animation: pulse 2s infinite;
		}
	}


	&--top {

		.dpit-controls__wrap {
			bottom: unset;
			top: 30px;

			align-items: flex-start;
			padding-top: 10px;

			@include atM {
				top: 20px;
			}

			@include atXS {
				top: 10px;
			}

		}

		.dpit-controls__button {
			@include z-index(14);

		}

		.dpit-controls__multi-close {
			position: relative;
			margin: 0 !important;
			transform: translateY(-10px);


			@include atM {

				transform: translateY(-20px);
			}

			.dpit-button-exit {
				border-radius: 2px;

				&--alt {
					border-radius: 50%;
				}
			}

			.dpit-box-sub-item {
				border-radius: 2px;
			}
		}
	}
}