@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {
	pointer-events: all;
	opacity: 1;
	transform: skew(-1deg, 0deg) translate(0, -50%);

	@media ((orientation: landscape) and (max-height: 480px)) {
		transform: skew(-1deg, 0deg);
		top: 0;
	}

	/*&.dp-pull-out-panel--top {
		top: 20px;
		transform: skew(-1deg, 0deg);
	}*/

	@include atXXS {
		transform: translateY(0) !important;
	}
}

@mixin inactive {
	transform: skew(-4deg, 7deg) translate(150%, -50%);
	pointer-events: none;
	opacity: 0;

	&.dp-pull-out-panel--top {
		//top: unset !important;
		//bottom: 20px;
		//transform: skew(-4deg, 7deg) translate(150%, 0);
	}

	@include atXXS {
		top: unset !important;
		transform: translateY(110%) !important;
	}
}

.dp-pull-out-panel {

	display: none;
	position: fixed;
	box-shadow: 0 0 12px 0 rgba(var(--dpu-color-2), 0.15);
	@include dpitBoxShadow;
	box-sizing: border-box;
	top: calc(50% - 60px);
	right: -7px;
	background: rgba(rgb(214, 214, 214), 0.97);
	@include z-index(14);
	padding: 0;
	border-bottom-left-radius: 2px;
	border-top-left-radius: 2px;
	@include inactive;
	flex-direction: column;
	@include transitionMultiFast(opacity, transform);
	user-select: none;

	@include atXXS {
		top: unset;
		max-width: unset;
		max-height: 100%;
		bottom: 0;
		left: 0;
		right: 0;
		border-radius: 0;
		background: rgb(214, 214, 214);
	}


	@include atXXS {
		min-width: unset;
	}

	&--active {
		display: flex;
		@include active;

		&.dp-pull-out-panel--in-transition {
			@include inactive;
		}
	}

	&--disabled {
		pointer-events: none;
	}

	&__drop-down {
		position: absolute;
		@include z-index(16);
		pointer-events: none;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		background-color: transparent;
		border-bottom-left-radius: 2px;
		border-top-left-radius: 2px;
		@include transitionFast(background-color, filter);

		&--active {
			pointer-events: all;
			background-color: rgba($text-color-dark, 0.5);
		}

		&--keep-target-ctrl {
			@include z-index(14);
		}

		@include atXXS {
			border-radius: 0;
		}

	}

	&__close-button {
		position: absolute;
		right: 35px;
		top: 17px;
		cursor: pointer;
		background-color: transparent;
		transform: rotate(-90deg);
		background-image: url('./../../../../../icons/dpit-select_arrow-down-dark.svg');
		background-position: center;
		background-size: 80%;
		background-repeat: no-repeat;
		border-radius: 2px;
		border: none;
		width: 38px;
		height: 38px;
		opacity: 0.9;
		outline: none;
		@include transitionMultiFast(opacity, transform, background-color);
		@include z-index(15);

		&:hover {
			transform: scale(0.9) rotate(-90deg);
			background-color: rgba(#fff, 0.3);
			opacity: 1;
		}

		@include atXXS {
			right: 20px;
			transform: rotate(0deg);

			&:hover {
				transform: scale(0.9);
			}
		}
	}

	&__title {
		@include primaryFontImportant(700, $text-color-dark, 19px, 1.167, normal, left);
		text-transform: uppercase;
		@include pullOutPanelPadding(0, 0);
		margin: 30px 80px 5px 0;
		@include z-index(15);

		@include atXXS {
			font-size: 16px !important;
			padding-left: 20px;
		}
	}
}