@import '../../../../../../../includes/assets/css/consts.scss';

.dp-target-selector {
	@include z-index(12, true);
	display: none;
	opacity: 0 !important;
	position: absolute;
	background: rgb(var(--dpu-color-3));
	outline: 2px solid $text-color-light;
	pointer-events: none;
	box-shadow: 0 0 3px 4px $text-color-dark;
	transition: top 0.02s ease-in, left 0.02s ease-in, width 0.02s ease-in, height 0.02s ease-in, opacity 0.3s ease-in-out;

	&--confirmed {
		animation: dpit-anim-gelatine 300ms;
		@include z-index(10, true);
		box-shadow: 0 0 0 var(--highlight-shadow-spread) rgb(var(--accent-contrast-inv-color));
	}

	&--trigger {
		background: rgb(var(--dpu-color-4));
	}

	&--mobile {
		background: rgb(var(--dpu-color-2));
	}

	&--active {
		opacity: 0.5 !important;
	}

}

.dp-visualized-target {
	cursor: crosshair;
}