@import '../../../../../../../includes/assets/css/consts.scss';
@import '../../consts/mixins.scss';


$activeElColorSemiDark: rgba(var(--dpu-color-2), 0.95);


.dpit-step-edit {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-bottom: 10px; // react on mouse enter was not triggered on the border
	margin-bottom: -10px;

	&__wrap {

		max-width: 530px;
		//max-width: 100%;
		@include pullOutPanelPadding(0, 10px);
		display: grid;
		grid-template-columns: 3fr 1fr;
		grid-template-rows: 1fr auto;
		gap: 21px;

		@include atXS {
			max-width: 458px;
			gap: 18px;
		}

		@include atXXS {
			grid-template-columns: 2fr 1fr;
			max-width: unset;

			gap: 11px;
			@include pullOutPanelPadding(0, 8px);
		}

		@include atXXXS {
			max-width: unset;
			width: 100%;

			display: flex;
			flex-direction: column-reverse;
			align-items: stretch;
			padding-bottom: 20px;
		}
	}



	&__ctrls {
		@include z-index(15);
		margin-top: 10px;
		margin-bottom: 20px;
		position: relative;
		display: flex;
		gap: 20px;
		background: $activeElColorSemiDark;
		background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.70) 25%, rgba(var(--dpu-color-2), 0.78) 100%);
		border-bottom-right-radius: 2px;
		border-top-right-radius: 2px;
		@include pullOutPanelPadding(20px, 7px);
		margin-right: 35px;
		flex-wrap: wrap;

		@include atXS {
			//padding: 10px 10px 5px 15px;
			margin-right: 0;
			gap: 15px;
			margin-bottom: 15px;
		}

		@include atXXS {
			min-width: 95%;
			justify-content: space-evenly;
			gap: 10px;
		}

		@include atXXXS {
			min-width: 100%;
			margin-right: 0px;
			gap: 5px;
			padding-top: 15px;
			padding-bottom: 12px;
		}
	}

	&__ctrls-2 {
		@include z-index(13);
	}

	&__target-type-box {
		border: 1px solid rgba(var(--dpu-color-g), 0.5);
		outline: 1px solid rgba(var(--dpu-color-g-text), 0.5);
		//box-shadow: 0 0 6px 0 rgba(var(--dpu-color-g), 0.5);

		&--no-target {
			border-color: rgba(var(--dpu-color-2), 0.5);
			outline-color: rgba(var(--dpu-color-2-text), 0.5);
		}

		&--skipped {
			border-color: rgba(var(--dpu-color-r), 0.5);
			outline-color: rgba(var(--dpu-color-r-text), 0.5);
		}
	}

	&__content {
		cursor: pointer;
		align-self: stretch !important;
		padding: 12px !important;
		border-radius: 2px !important;
		transition: background-color $transition-time-fast ease-in-out !important;
		outline: 1px solid rgba($text-color-dark, 0.5) !important;
		border: none !important;
		min-width: unset !important;
		max-height: 100px;

		resize: none;

		background: rgba($background-color-light, 0.9) !important;
		@include primaryFontImportant(400, $text-color-dark, 14px, 16px, normal);

		&:hover {
			background: $background-color-light !important;
		}

		@include atXXS {
			width: unset !important;
			max-height: 53px;

			&--higher-on-mobile {
				margin-bottom: 250px !important;
			}
		}


		@media ((orientation: landscape) and (max-height: 480px)) {
			max-height: 60px;
		}
	}

	&__signals {
		display: flex;
		justify-content: center;
		margin-top: 3px;
		margin-left: -2px;


		&--disabled {
			@include disabled-ctrl-item;
		}

		.dpit-target-indicator:nth-child(2) {
			padding: 0 2px;
			z-index: 0;
		}
	}

	&__target-ctrl {
		@include z-index(11);
		min-width: 50px;
		margin-top: -6px;

		.dpit-multi-item-box__menu {
			padding: 4px;
			transform-origin: calc(100% - 25px) 25px;

			.dpit-multi-item-box__item {
				.dpit-box-sub-item {
					padding: 0;
					overflow: hidden;
					min-width: unset;
					cursor: default;

					@include atS {
						text-align: left;
					}

					@include atXXS {
						min-width: 180px;
						transform: none;
					}
				}

				&:nth-child(2) {
					.dpit-box-sub-item {
						margin-top: 5px;
					}
				}
			}

			@include atS {
				transform-origin: 25px 25px;
				right: unset;
				left: 0;
				align-items: flex-start;
			}

			@include atXXS {
				transform: none;
			}
		}
	}

	&__arrow-down {
		@include transitionMultiFast(opacity, height);
		width: 22px;
		height: 22px;
		opacity: 1;
		cursor: pointer;
		pointer-events: all;
		align-self: flex-end;
		margin-right: 10px;

		@include atXS {
			margin-left: 10px;
			margin-right: 0px;
		}

		&--hidden {
			height: 0;
			width: 0;
			opacity: 0;
			pointer-events: none;
		}

		&--up {
			transform: rotate(180deg);
		}

		&--l {
			width: 27px;
			height: 27px;
		}

		&:hover {
			opacity: 1;
		}

		path {
			transition: stroke $transition-time-fast ease-in-out;
			stroke: rgba(var(--dpu-color-2-text), 1);
		}

	}

	&__button {

		cursor: pointer;
		background-color: transparent;
		opacity: 0.95;
		background-position: center;
		background-size: 80%;
		background-repeat: no-repeat;
		width: 42px;
		height: 42px;
		border: none;
		outline: none;
		@include transitionMultiFast(opacity, transform);

		&--target {
			border-radius: 50%;
			background-image: url('./../../../../../icons/dp-cross-hairs-target.svg');
		}


		&:hover {
			opacity: 1;
		}

		&--url {
			background-image: url('./../../../../../icons/dpit-directions-solid.svg');
			fill: #fff;
			margin-top: -2.5px;

			&:hover {
				transform: scale(1.05) rotate(-8deg);
			}
		}
	}

	&__sub-button {
		margin-top: 7px;
		min-width: 180px;
	}

	// interaction
	&__switch.toggleContainer {

		@include z-index(14);
		min-width: 120px;
		@include panelControlItemHovFoc($transition-time-fast);
		display: flex;
		max-width: 200px;

		@include atXXS {
			min-width: 100px;
			max-width: 100px;
		}

		@include atXXXS {

			max-width: 100px;
		}

		.toggleOption {
			padding: 12px 5px;
		}

	}



	&__navig {
		margin-left: auto;
		margin-right: 0;
		display: flex;
		justify-content: flex-end;

		@include atXS {
			justify-content: stretch;
			margin-left: 0;
			width: 100%;
			transform: none;
		}

		&__button {
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			color: rgb(var(--dpu-color-2-text));
			background-color: rgb(var(--dpu-color-2));
			padding-top: 16px;
			padding-bottom: 16px;
			min-width: 100px;
			text-decoration: none;
			@include transitionMultiFast(background, color, opacity);
			outline: none;
			border: none;

			opacity: 0.87;
			@include primaryFont(600, $text-color-light, 14px, 16px, normal, center);

			&.dpit-hovered,
			&:hover {
				opacity: 1;
			}

			@include atXS {
				min-width: 50px;
				padding-top: 15px;
				padding-bottom: 15px;
			}

			@include atXS {
				width: 25%;
			}

			@include atXXS {
				padding-top: 15px;
				padding-bottom: 15px;
				min-width: unset;
				padding-left: 20px;
				padding-right: 20px;
			}

			&--add {
				background-color: rgb(var(--dpu-color-g));
				color: rgb(var(--dpu-color-g-text));
			}



			&--back {
				border-top-left-radius: 2px;
				color: rgba(var(--dpu-color-2-text), 1);
				background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.75) 25%, rgba(var(--dpu-color-2), 0.82) 100%);

				&.dpit-hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.8) 25%, rgba(var(--dpu-color-2), 0.86) 100%);
				}

				@include atXS {
					border-top-left-radius: 0px;
				}

			}

			&--next {
				color: rgba(var(--dpu-color-2-text), 1);
				background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.8) 25%, rgba(var(--dpu-color-2), 0.86) 100%);

				&.dpit-hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.75) 25%, rgba(var(--dpu-color-2), 0.82) 100%);

				}

			}

			&--remove {
				background: linear-gradient(315deg, rgba(var(--dpu-color-r), 0.8) 25%, rgba(var(--dpu-color-r), 0.86) 100%);
				color: rgba(var(--dpu-color-r-text), 1);

				&.dpit-hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-r), 0.75) 25%, rgba(var(--dpu-color-r), 0.82) 100%);
				}

			}

			&--disabled {
				/*@include disabled-ctrl-item;
				cursor: default;
				pointer-events: none;*/
			}
		}

		&__btn-ico {
			width: 20px;

			&--remove {
				width: 22px;
			}

			&--right {
				transform: rotate(180deg);
				width: 18px;
			}

			&--left {
				width: 18px;
			}


		}
	}
}