@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {
	margin-right: 0;
	opacity: 1;
}

@mixin inactive {
	opacity: 0;
	margin-right: -68px;
}

.dpit-multi-item-box {
	position: relative;
	display: none;
	min-width: 68px;
	min-height: 48px;
	@include transitionMultiFast(opacity, margin-right);
	@include inactive;

	&__menu {
		position: absolute;
		top: 0;
		right: 0;
		padding: 10px 10px 10px;
		pointer-events: all;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		transition: transform $transition-time-med ease-in;
		transform: rotate(-90deg);
		transform-origin: calc(100% - 34px) 34px;

		&--opened {
			transform: none;
		}
	}

	&--active {
		display: block;
		@include active;

		&.dpit-multi-item-box--in-transition {
			@include inactive;
		}
	}

	&--slide-down {
		width: 100%;
		min-height: 28px;
		margin-right: 0;

		.dpit-multi-item-box__menu {
			transform-origin: unset;
			padding: 0;
			display: flex;
			width: auto;
			@include transitionFast(margin);
			//transition: margin $transition-time-fast ease-in;
			transform: none;


			@include atXS {
				right: unset;
				left: 0;
			}

		}

		&.dpit-multi-item-box--active {
			&.dpit-multi-item-box--in-transition {
				opacity: 0;
				margin-right: 0;
				margin-top: -10px;
			}

			.dpit-multi-item-box__menu--opened {
				margin-top: -10px;
			}
		}
	}

	&__item {
		width: auto;
		height: auto;
	}
}