@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-button-close-popup {
	pointer-events: all;
	cursor: pointer;
	background-color: transparent;
	color: $text-color-dark;
	border-radius: 50%;
	border: none;
	width: 30px;
	height: 30px;
	opacity: 1;
	outline: none;
	@include transitionMultiFast(transform, background-color);

	&:hover {
		transform: scale(0.9) rotate(90deg);
		background-color: rgba(41, 41, 41, 0.3);
	}

}