@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {

	margin-right: 0;
	opacity: 0.95;
}

@mixin inactive {
	margin-right: -300px;
	opacity: 0;
}

.dpit-popup-hint {

	@include z-index(15);
	pointer-events: all;
	max-width: 300px;
	background-color: rgba(255, 255, 255, 0.733);
	padding: 7px;
	padding-top: 37px;
	@include transitionMultiFast(margin-right, opacity, background-color, color);
	border-radius: 2px;
	margin: 0 10px;


	@include primaryFont(400, $text-color-light, 14px, 1.5em);

	@include inactive;
	text-align: right;
	margin-bottom: 10px;

	&--dismissible {
		padding-top: 7px;
	}

	&__content {
		padding: 0 23px 10px;

		&__title {
			text-align: center;
			font-size: 18px;
			text-transform: uppercase;
			font-weight: bold;
		}

		&__desc {
			font-size: 14px;
			text-align: center;
			margin-bottom: 0;
			line-height: 1.3em;
			margin: 10px auto 0;
			width: 95%;
			word-wrap: break-word;
		}

		&__cta {
			text-align: center;
			pointer-events: all;
			cursor: pointer;
			border-radius: 2px;
			padding: 5px 10px;
			transition: color $transition-time-fast ease-in-out, background-color $transition-time-fast ease-in-out;
			background-color: rgba(var(--dpu-color-r), 0.1);
			margin-bottom: 5px;

			&:hover {
				color: rgba(var(--dpu-color-r-text), 1);
				background-color: rgba(var(--dpu-color-r), 0.9);
			}
		}

		&__after-cta {
			text-align: center;
			font-size: 12px;
			line-height: 1.2em;
		}
	}

	&__transition {
		&-enter-done {
			@include active;
		}
	}

	&__close-button {
		margin-left: auto;
		display: none;
		justify-content: center;
		align-items: center;

		svg {
			width: 55%;
		}

		&--active {
			display: flex;

		}
	}

	&--info {
		background-color: rgba(171, 202, 219, 0.9);
		color: $text-color-dark;
	}

	&--error {
		background-color: $orange-color;
		color: $text-color-light;

		.dpit-popup-hint__close-button {
			color: $text-color-light;
		}
	}
}

.dpit-wrap.sticky {
	.dpit-popup-hint {
		margin-left: 5px;
		margin-right: 15px;
	}
}