@import '../../../../../../../includes/assets/css/consts.scss';
@import '../../consts/mixins.scss';

.dpit-step-edit-item {
	width: auto;
	//margin-right: 20px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	@include transitionFast(opacity);

	&:first-child {
		@include z-index(13);
	}

	&--disabled {
		@include disabled-ctrl-item;
		pointer-events: none;
	}

	&:nth-child(2) {
		@include z-index(13);
		padding-right: 3px;
		margin-left: -7px;
	}


	@include atXXS {
		transform: none;
	}

	&__select {
		display: inline-block;
		width: auto;
		min-width: 150px;
		min-height: unset;
		height: 38px;

		@include atXXS {
			min-width: 100px;
			max-width: 100px;
		}
	}

	&__label {
		@include transitionFast(opacity);
		@include primaryFont(300, rgba($text-color-light, 0.95), 14px, 1.14, normal, left);
		text-transform: uppercase;
		vertical-align: middle;
		padding-bottom: 7px;

		@include atXS {
			font-size: 13px;
		}

		@include atXXS {
			text-transform: unset;
			font-size: 14px;
		}
	}

	&--in-column {
		display: flex;
		flex-direction: column;
		align-items: stretch;
		max-width: 150px;

		.dpit-select {
			width: 100%;

			@include atXXS {
				max-width: unset;
			}
		}

		@include atXXXS {
			max-width: unset;
		}
	}

	&--interaction {
		@include atXXS {
			.dpit-multi-item-box__menu--opened {
				left: -70px !important;
			}
		}
	}

	&--ta-wrap {
		flex: 1 1 95%;
		align-items: flex-start;
	}

	&--on-light-bg {
		&.dpit-step-edit-item--disabled {
			opacity: 0.45;
		}

		.dpit-step-edit-item__label {
			@include primaryFont(500, $text-color-dark, 11px, 1.3, 0.83px, left);

			margin: 0;

			@include atXXS {
				text-transform: none;
			}

			&__icon {
				display: inline-block;
			}
		}

		.dpit-select {
			&:not(.dpit-select--native) {
				outline: 1px solid rgba($text-color-dark, 0.5) !important;
			}
		}
	}
}