@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-step-meta-data {

	@include activeTransitionFade(flex);
	background: linear-gradient(315deg, rgba(var(--dpu-color-2-text), 0.95) 25%, rgba(var(--dpu-color-2-text), 0.98) 100%);
	padding: 15px;
	border-radius: 2px;
	pointer-events: all;
	align-items: flex-end;
	flex-direction: column;
	border: 1px solid $text-color-very-dark;
	min-width: 350px;
	gap: 10px;
	overflow: auto;


	@include atTouchDev {
		padding: 8px 15px 15px;

	}

	@include atS {
		min-width: 300px;
	}

	@include atXXS {
		position: fixed;
		left: 10px;
		right: 10px;
		bottom: 10px;
		max-height: calc(100vh - 20px);
	}

	@include atXXXS {}


	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		gap: 10px;

		&__label {
			color: $text-color-dark;
			display: inline;
			//white-space: pre-wrap;
			font-size: 12px;
			line-height: 1.1;
			letter-spacing: 0.5px;
			text-align: right;

			//white-space: nowrap;
			text-transform: uppercase;
			//flex: 1 1 auto;

			@include atS {
				text-transform: none;
				//text-wrap: wrap;
			}

			@include atXXXS {
				//max-width: 100px;

			}
		}



		&__number {
			max-width: 96px;
		}

		&__hint {
			display: block;
			transform: translate(-5px, -3px);
		}

		&__comp {
			flex: 0 0 auto;
		}

		.toggleContainer.dpit-switch {
			background-color: rgba($text-color-dark, 0.7);
			border: 1px solid rgba($text-color-dark, 0.8);

			.toggleOption:not(.selected) {
				color: $text-color-light;
			}

			.toggle.dpit-switch__toggle {
				outline: 1px solid $text-color-dark;


				&.dpit-switch__toggle--off {
					outline-color: rgb(var(--dpu-color-r));
				}
			}
		}
	}

	&__arrow-up {
		//margin-bottom: 8px;
		margin-right: -6px !important;
		margin-left: 0 !important;
		border-radius: 2px;
		padding: 4px;
		display: none;

		@include atTouchDev {
			display: block;
		}

		margin-right: 5px;

		@include atXS {
			margin-left: -10px !important;
			margin-right: 0px !important;
		}



		@include atXS {
			align-self: flex-start !important;
			margin-right: 0;
			margin-left: -10px;
		}

		path {
			stroke: $text-color-dark !important;
		}


	}
}