@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-box-sub-item {
	--dpit-shadow-alpha: 0.5;
	min-width: 150px;
	margin-top: 10px;
	cursor: pointer;
	padding: 10px 10px 10px 14px;
	border-radius: 0px;
	@include transitionMultiFast(color, background-color, transform, opacity, margin-right);
	//transition: color .8s ease-in-out, background-color .5s ease-in-out, transform 300ms ease-in-out, opacity 300ms ease-in-out, margin-right 300ms ease-in-out;
	@include dpitBoxShadowOff0(3px);
	@include z-index(13);
	background-color: rgba(var(--dpu-color-1-light), 0.97);
	border: 1px solid $text-color-very-dark;
	//outline: 1px solid rgba($text-color-light, 0.9);
	@include primaryFont(400, $text-color-dark, 14px, 1.5, normal, right);
	@include activeTransitionFade;

	&:hover {
		transform: scale(1.05);
		background-color: rgba(var(--dpu-color-1-light), 1);
	}

	&--semi-active {
		color: rgba($text-color-dark, 0.65);
	}

	&--slower-opacity {
		transition: color .8s ease-in, background-color .5s ease-in, transform $transition-time-fast ease-in, opacity $transition-time-med ease-in;

		&.dpit-box-sub-item--exiting {
			transition: color .8s ease-in, background-color .5s ease-in, transform $transition-time-fast ease-in, opacity 250ms ease-in;
		}
	}

}