@import '../../../../../../../includes/assets/css/consts.scss';


@mixin activeSlideUp($padding: 0, $margin: 0) {
	opacity: 0;
	height: 0;
	pointer-events: none;
	@include transitionMultiFast(height, width, opacity, padding, background-color);

	&--active {
		opacity: 1;

		@if $padding {
			padding: $padding;
		}

		@if $margin {
			margin: $margin;
		}

		height: unset;
		pointer-events: all;
	}
}

@mixin activeSlideLeft($padding, $margin, $opacity: 1) {
	opacity: 0;
	width: 0;
	height: 0;
	pointer-events: none;
	@include transitionMultiFast(width, opacity, padding, background-color, transform);

	&--active {
		opacity: 1;

		@if $padding {
			padding: $padding;
		}

		@if $margin {
			margin: $margin;
		}

		@if $opacity {
			opacity: $opacity;
		}

		width: unset;
		height: unset;
		pointer-events: all;
	}
}

.dpit-target-type-box {
	display: flex;
	flex-direction: column;
	width: 300px;
	padding: 0;

	@include atXXS {
		width: 90vw;
	}

	&__control {
		display: flex;
		padding: 10px 10px 12px;
		background-color: rgb(var(--dpu-color-2));
		flex-direction: row-reverse;
		align-items: center;
		gap: 20px;

		@include atS {
			flex-direction: row;
		}

		&__icon {
			flex-shrink: 0;

			svg {
				width: auto;
				height: 30px;
				margin-bottom: -6px;
				opacity: 1;
			}
		}

		&__skip.toggleContainer.dpit-switch {
			// skip on desktop/mobile
			min-width: 120px;

			.toggleOption {
				font-size: 11px;
				padding: 8px;

				&:nth-child(2) {
					color: rgba(var(--dpu-color-r-text), 1);
				}

			}
		}

		&__label {
			text-transform: uppercase;
			@include primaryFont(500, rgba(var(--dpu-color-2-text), 1), 13px, 1, 1.5px);
		}
	}

	&__title {
		text-align: center;
		max-width: 90%;
		font-size: 12px;
		line-height: 1.1;
		@include activeSlideUp(0 0 3px 0);
	}

	&__row {
		display: flex;
		justify-content: center;
		align-items: stretch;
		padding-bottom: 3px;
		gap: 5px;

		&--middle {
			align-items: center;
			max-width: 90%;
		}
	}

	&__trash {
		cursor: pointer;
		display: none;
		width: 15px;
		height: 15px;
		margin-left: 5px;
		margin-top: -3px;
		transform-origin: center;
		@include transitionFast(opacity);

		&--active {
			display: block;
		}


		&:hover {
			opacity: 0.75;
		}

		svg {
			width: 14px;
			height: 14px;

			path {
				@include transitionFast(fill);
				fill: rgba(var(--dpu-color-r), 0.9);
			}
		}
	}


	&__value {
		font-size: 12px;
		text-overflow: ellipsis;
		line-height: 1.1;
		display: block;

		overflow: hidden;
		white-space: nowrap;
		color: rgba($text-color-dark, 1);


		&--no-target {
			text-overflow: unset;
			text-align: center;
			overflow: unset;
			white-space: normal;
			color: rgba($text-color-dark, 0.9);
		}

		&--skipped {
			text-overflow: unset;
			margin: 0 auto;
			text-align: center;
			overflow: unset;
			white-space: normal;
			color: rgba(var(--dpu-color-r), 1);
		}
	}


	&__button {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 2px;
		background-color: rgba(var(--dpit-uac-prim-bg), 0.9);
		cursor: pointer;

		@include activeSlideUp(8px 30px);


		&__icon {
			width: 22px;
			height: 22px;
			margin: -3px 3px -1px 0;
			transition: transform 0.3s ease-in-out;

			svg {

				path {
					transition: fill 0.3s ease-in-out;
					fill: rgb(var(--dpit-uac-prim));
				}
			}
		}

		&__label {
			margin-left: 3px;
			@include primaryFont(600, rgb(var(--dpit-uac-prim)), 14px, 1.1);
			text-transform: uppercase;
			padding: 3px 0;
		}

		&:hover {
			background-color: rgb(var(--dpit-uac-prim-bg));

			.dpit-target-type-box__button__icon {
				transform: rotate(90deg) scale(0.95);
			}
		}


		&--half {
			flex-basis: unset;

			&.dpit-target-type-box__button--active {
				padding: 10px 20px;
			}

			.dpit-target-type-box__button__icon {
				width: 16px;
				height: 16px;
			}

			.dpit-target-type-box__button__label {
				font-size: 12px;
				text-align: left;
				font-weight: 600;
			}

			&:hover {
				.dpit-target-type-box__button__icon {
					transform: scale(1.05);
				}
			}
		}
	}

	&__main {
		padding: 10px 12px;
		display: flex;
		flex-direction: column;
		gap: 5px;
		align-items: center;
	}


}