@import '../../../../../../../includes/assets/css/consts.scss';
@import '../../consts/mixins.scss';

.toggleContainer.dpit-switch {

	position: relative;
	background-color: rgba(#ffffffd2, 0.45);
	border-radius: 2px;
	line-height: 1 !important;
	@include panelControlItemHovFoc($transition-time-fast);
	display: flex;
	box-sizing: border-box;

	.toggleOption {
		position: relative;
		display: inline-block;
		z-index: 1;
		text-wrap: nowrap;
		cursor: pointer;
		@include transitionMultiFast(color, opacity, font-weight);
		@include primaryFont(300, rgb(var(--dpu-color-g-text)), 14px, 14px, normal, center);
		opacity: 0.65;
		padding: 12px;
		overflow: hidden;
		text-overflow: ellipsis;

		&:nth-child(2) {
			color: rgba(var(--dpu-color-r-text), 0.8);
		}

		&.selected {
			opacity: 1;
			font-weight: 600;
			cursor: default;
		}

		&:not(.selected) {
			color: $text-color-very-dark;
		}
	}


	.toggle.dpit-switch__toggle {
		z-index: 0;
		position: absolute;
		border-radius: 2px;
		height: 100%;
		top: 0;
		left: 0;
		@include transitionMultiFast(background-color, width, transform);
		background-color: rgba(80, 80, 80, 0.555);

		&.dpit-switch__toggle--on {
			background-color: rgba(var(--dpu-color-g), 0.95);
			width: 50% !important;
		}

		&.dpit-switch__toggle--off {
			background-color: rgba(var(--dpu-color-r), 0.83);
			width: 50% !important;
			transform: translateX(100%) !important;
		}
	}

	&.dpit-switch--half-size {
		.toggleOption {
			padding: 4px 9px;
			font-size: 10px;
			min-width: 50px;
		}
	}

	&.dpit-switch--dark-bg {
		background-color: rgba($text-color-dark, 0.7);

		.toggleOption {
			&:not(.selected) {
				color: $text-color-light;
			}
		}
	}

	&.dpit-switch--hidden {
		opacity: 0;
		pointer-events: none;
		height: 0;

		.toggleOption {
			font-size: 0;
		}
	}

}