@import './../../../../../../../includes/assets/css/consts.scss';
@import '../../consts/mixins.scss';

.dpit-select {

	@include transitionFast(opacity);

	// native select
	&--native {
		background: url('./../../../../../icons/dpit-select_arrow-down-dark.svg') calc(100% - 5px) 10px no-repeat, $background-color-light !important;
		@include panelControlItemHovFoc($transition-time-fast);
		border-radius: 2px !important;
		@include primaryFontImportant(600, $text-color-dark, 14px, 21px, normal, left);
		padding: 8.5px 35px 8.5px 12px;
		appearance: none;
		background-size: 17px !important;
		outline: 0;
		overflow: hidden;
		text-overflow: ellipsis;

		option {
			overflow: hidden;
			text-overflow: ellipsis;

			@include atXS {
				//font-size: 15px !important;
			}
		}
	}

	// custom select react-select
	&__control {
		background-color: $background-color-light;
		@include panelControlItemHovFoc($transition-time-fast);
		padding-left: 4px;
		padding-right: 4px;
		border: none !important;
		box-shadow: none !important;
	}

	&__menu {
		border-radius: 2px;

	}

	&__menu-list {
		border-radius: 2px;
		padding-top: 0 !important;
		padding-bottom: 0 !important;
		box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.1), 0 0 12px 0 rgba(0, 0, 0, 0.25);
	}

	&__indicator-separator {
		display: none;
	}

	&__dropdown-indicator {

		//pointer-events: none;
		padding: 0px !important;

		.dpit-select-indicator {
			cursor: pointer !important;
			background-position: center;
			background-size: 80%;
			background-repeat: no-repeat;
			width: 24px;
			height: 24px;

			&--opened {
				width: 20px;
				height: 20px;
				background-image: url('./../../../../../icons/dp-close-cross-dark.svg');
				transform: translateX(-5%);
			}

			&--closed {
				background-image: url('./../../../../../icons/dpit-select_arrow-down-dark.svg');
			}
		}
	}

	&__single-value {

		opacity: 1;

		&:disabled {
			opacity: 0.5;
		}

		transition: opacity $transition-time-fast;
		@include primaryFont(600, $text-color-dark, 14px, 21px, normal, left);
		//text-transform: uppercase !important;
	}

	&__placeholder {
		@include primaryFont(300, $text-color-placeholder, 14px, 21px, normal, left);
		//text-transform: uppercase !important;
	}

	&__input {
		@include primaryFont(400, $text-color-dark, 14px, 21px, normal, left);
		//text-transform: uppercase !important;

		input {
			@include primaryFontImportant(400, $text-color-dark, 14px, 21px, normal, left);
			height: unset !important;
			margin-bottom: 0 !important;

			&:focus {
				border: none !important;
				box-shadow: none !important;
			}
		}
	}

	&__option {


		padding: 10px 20px !important;
		overflow: hidden;
		text-overflow: ellipsis;
		@include primaryFont(300, $text-color-dark, 14px, 21px, normal, left);
		background-color: transparent;

		&:first-child {
			padding-top: 15px !important;
		}

		&:last-child {
			padding-bottom: 12px !important;
		}


		&--is-selected {
			font-weight: 600;
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.5);
		}

		&--is-focused {
			font-weight: 600;
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.8);
		}

		&:active {
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.9);
		}

		@include atXS {
			padding: 5px 15px !important;

			&:first-child {
				padding-top: 10px !important;
			}

			&:last-child {
				padding-bottom: 8px !important;
			}
		}
	}

	@include atHoverAbleDev {
		&__menu-list {
			scrollbar-width: thin;
			scrollbar-color: rgba(var(--dpit-uac-main-menu-bg), 0.5) rgba(var(--dpit-uac-main-menu-bg), 0.1);

			&::-webkit-scrollbar {
				width: unit(15);
			}

			&::-webkit-scrollbar-track {
				border-radius: 2px;

				background-color: rgba(var(--dpit-uac-main-menu-bg), 0.1);
			}

			&::-webkit-scrollbar-thumb {
				border-radius: 2px;
				background-color: rgba(var(--dpit-uac-main-menu-bg), 0.5);
			}
		}
	}
}