@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {
	opacity: 1;
	transform: translate(-50%, -50%);

	@include atXS {
		transform: translate(-50%, -50%);
	}

	pointer-events: all;
}

@mixin inactive {
	opacity: 0;
	transform: translate(150%, -60%);
	pointer-events: none;
}

.dpit-popup-promo {
	position: fixed;
	@include z-index(13);
	min-width: 500px;
	top: 50%;
	left: 50%;
	background-color: $text-color-light;
	padding: 7px;
	padding-top: 37px;
	border-radius: 2px;
	@include inactive;
	@include transitionMultiFast(transform, opacity);


	@include atXS {
		min-width: 80%;
	}

	&--dismissible {
		padding-top: 7px;

	}

	&__content {
		padding: 10px 25px 15px;
		margin-top: -30px;
		display: flex;
		flex-direction: column;
		align-items: center;

		@include atS {
			padding: 10px 25px 15px;

		}

		&__image {
			width: 70%;
			max-width: 150px;
			height: auto;
			text-align: center;
			margin-bottom: 10px;
		}

		&__title {
			text-align: center;
			font-size: 18px;
			font-weight: bold;
		}

		&__desc {
			font-size: 14px;
			text-align: center;
			margin-bottom: 0;
			line-height: 1.3em;
			margin: 10px auto;
		}
	}


	&__buttons {
		margin-top: 10px;
		display: flex;
		gap: 15px;

		@include atXS {
			flex-direction: column;
		}

		&__btn {

			cursor: pointer;
			padding: 10px 20px;
			border-radius: 2px;
			@include transitionMultiFast(background-color, color, transform);
			border: none;

			font-size: 16px;
			line-height: 1.3;

			outline: 2px solid rgba($text-color-dark, 0.9);
			color: $text-color-dark;

			&:hover {
				outline-color: $text-color-dark;
				text-decoration: none;
			}

			&--yes {
				font-weight: 600;
				background-color: rgba(255, 205, 3, 0.788);

				&:hover {
					background-color: rgb(255, 205, 3);
				}
			}

			&--no {
				font-weight: 500;
				background-color: rgba($text-color-placeholder, 0.3);

				&:hover {
					background-color: rgba($text-color-placeholder, 0.2);
				}
			}
		}
	}

	&__transition {
		&-enter-done {
			@include active;
		}

		&-exit {
			transform: translate(-150%, -40%);
		}
	}

	&__close-button {
		margin-right: 5px;
		margin-top: 3px;
		margin-left: auto;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 55%;
		}
	}
}