@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-button-exit {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 0;
	transform: rotate(90deg) !important;
	border-radius: 2px;
	width: 48px;
	height: 48px;
	color: rgb(var(--dpu-color-r-text));
	background-color: rgba(var(--dpu-color-r), 0.9);
	padding: 0;
	cursor: pointer;
	@include transitionMultiFast(transform, border-radius, color, background-color);

	@include dpitBoxShadowOff0(4px);
	border: none;
	outline: none;
	opacity: 1;


	@include atXXS {
		top: 20px;
		right: 21px;
	}

	&__icon {
		width: 40%;
	}

	&--alt {
		transform: none;
		border-radius: 50%;
		color: rgb(var(--dpu-color-r-text));
		background-color: rgba(var(--dpu-color-r), 0.9);
	}
}