@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-target-indicator {


	path {
		@include transitionFast(fill);
		fill: rgb(var(--dpu-color-g));
	}

	&--small {
		.dpit-target-indicator__icon {
			height: 15px;
			margin: 0 2.5px 0 2.5px;
		}
	}

	&--scale-on-hover {
		.dpit-target-indicator__icon {
			transform-origin: center;
			@include transitionFast(transform);
			overflow: visible;

			&:hover {
				transform: scale(1.08);
			}
		}
	}

	&--no-target {
		path {
			fill: rgba(var(--dpu-color-2-text), 0.4);
		}
	}

	&--skipped {
		path {
			fill: rgba(var(--dpu-color-r), 1);
		}
	}

	&--clickable {
		cursor: pointer;
	}
}