@import '../../../../../../../includes/assets/css/consts.scss';

.dpit-anim-rb-builder {
	pointer-events: none;
	position: absolute;
	display: flex !important;
	justify-content: center;
	line-height: 1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 3px !important;
	@include transitionFast(opacity);
	opacity: 0 !important;
	@include z-index(14);

	&.active {
		opacity: 1 !important;
	}

	ul {
		margin: 0 auto;
		padding: 0;
		display: flex;
		justify-content: center;
		flex-wrap: nowrap;
		align-items: center;

		li {
			margin: 2px;
			width: 8px;
			height: 8px;
			list-style: none;
			border-radius: 50%;
			text-decoration: none;
			outline: none;
			transform-origin: center;
			overflow: hidden;
			display: block;
			transition: transform 0.2s ease-out, background-color 0.185s ease-out;

			&.dpit-bullet-on {
				background-color: #fff;

				&--1 {
					transform: scale(1.3);
					background-color: rgba(255, 255, 255, 0.918);
				}

				&--2 {
					transform: scale(1.15);
					background-color: rgba(255, 255, 255, 0.718);
				}
			}
		}
	}
}